  export default {
    data () {
      return {
        navList: [],
        navNameList: [],
        chooseList: [],
        index: 0,
        coverImg: '',
        chooseItem: {}
      }
    },

    onLoad () {
      this.$http.getNavList().then(res => {
        if (res.status === 200) {
          this.navList = res.data.slice(1)
          this.navNameList = res.data.slice(1).map(item => {
            return item.type_name
          })
        }
      })
    },

    methods: {
      bindPickerChange (e) {
        this.index = e.target.value
      },

      getItemName (e) {
        this.chooseItem = this.navList[e.target.value]
      },

      toLive () {
            // this.getnav(`/pages/live/Anchor`)
            // return
        if (!this.coverImg) {
          this.$toast('请上传直播封面图')

          return false
        }

        if (!this.chooseItem.id) {
          this.$toast('请选择直播分类')

          return false
        }

        this.$http.launchalive({
          cover: this.coverImg,
          cateid: this.chooseItem.id
        }).then(res => {
          if (res.status === 200) {
            uni.setStorageSync('pullurl', res.data)
            this.getnav(`/pages/live/Anchor?url=${res.data}`)
          }
        }).catch(err => {
          console.log(err)
        })
      },

      upLoadImg () {
        this.$utils.chooseImg().then(res => {
          this.$loading('上传中')
          uni.uploadFile({
          	url: 'https://www.bmsapp.cn/apicloud/find/uploadspic',
            filePath: res.tempFilePaths[0],
            name: 'file',
            success: res => {
							const data = JSON.parse(res.data)
							if (data.code === 200) {
                this.coverImg = data.data.src.wz
                this.$hideLoading()
							} else {
								this.$toast(data.mess)
							}
						},
						
						fail: err => {
							this.$hideLoading()
						}
          })
        })
      }
    }
  }
